.Registration-form{
    background-color:bisque;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 400px;
    
    height: 400px;
    padding-left: 15px;
    font-size: medium;
    font-variant: small-caps;
    margin-left: 500px;
    margin-top: 120px;
    font-family: Verdana, sans-serif;
    box-shadow: 0 0 20px #ff000030;

}
.Input-label {
    font-size: medium;
    font-variant: small-caps;
    font-family: Verdana, sans-serif;
}

.Text-input {
    width: 95%;
    color: salmon;
    font-size: 15px;
    border: none;
    border-bottom: 1px solid #d3d3d3;
}

.Button {
    font-size: 16px;
    margin-top: 20px;
    background-color:salmon;
    padding: 10px;
    border: none;
    box-shadow: 0 0 20px #ff000030;
    width: 95%;
    color: #ffffff;
}

.Alert-label {
    font-size: medium;
    font-variant: small-caps;
    font-family: Verdana, sans-serif;
    color: red;
}



.Link {
font-size: medium;
font-variant: small-caps;
font-family: Verdana, sans-serif;
color: darksalmon;
margin-left: -100px;
margin-top: 400px;
}

